<template>
    <div>
        <el-scrollbar class="pepoleMessage">
            <ul class="messageBox">
                <li class="mag" v-for="(comment,index) in comments" :key="index">
                    <img class="img" :src="comment.headerimageurl" alt/>
                    <div class="commont">
                        <p class="topFont">
                            {{comment.name | nameIsNull}} <span style="display: inline-block;font-size: 14px">发表于：{{parseInt(comment.date) | timeIs }}</span>
                        </p>
                        <p class="bottomFont">{{comment.comment}}</p>
                    </div>
                </li>
            </ul>
        </el-scrollbar>
        <div class="messageSenBox">
            <router-link to="message">
                <button class="senMessage">写新留言</button>
            </router-link>
        </div>


    </div>
</template>

<script>
    import timeNow from "../../../../config/timeNow";

    export default {
        name: "Pepolemessage",
        computed: {
            comments() {
                return this.$store.state.comments;
            }
        },
        filters: {
            timeIs(value) {
                return timeNow(value, false);
            },
            nameIsNull(value) {
                if (value) return value;
                else return "匿名";
            }
        }
    };
</script>

<style lang="scss" scoped>
    @import "../../../../style/_statement";

    * {
        font-size: 16px;
    }

    .messageBox {
        @include verticalCenter;
    }

    .commont {
        margin-left: 5px;
        margin-top: 3px;
    }

    .bottomFont {
        width: 435px;
        height: 25px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }

    .img {
        min-width: 45px;
        width: 45px;
        height: 45px;
        border-radius: 50%;
    }

    .mag {
        display: flex;
        align-items: center;
        width: 80%;
        height: 50px;
        margin: 10px 0;
        line-height: 25px;
        font-family: 文鼎CS大黑;
        font-weight: 600;
    }

    .pepoleMessage {
        margin-top: 10px;
        height: 425px;
        overflow-x: hidden;
    }

    .messageSenBox {
        width: 100px;
        margin: 10px auto;
    }

    .senMessage {
        width: 100px;
        height: 37px;
        background-color: rgba(0, 0, 0, 0);
        color: white;
        border-radius: 10px;
        solid-color: white;
        outline: none;
    }

</style>
