<template>
    <div class="musicPlayer">
        <div class="musicBody" :class="isShow ? playerTop : ''">
            <MusicPlayerTop
                :musicControl="musicControl"
                :musicListShow="musicListShow"
                :musicIsPlay="musicIsPlay"
                :nextSong="nextSong"
                :previousSong="previousSong"
                :musicPlanShow="musicPlanShow"
            />
            <MusicPlayerBottom
                class="Mbottom"
                :class="(musicControl.isListShow && isShow) ? playerBottom : ''"
                :musicControl="musicControl"
                :changeMusic="changeMusic"
            />
        </div>
        <MusicShowButton :musicPlanShow="musicPlanShow" :isControlShow="isControlShow" />
        <audio id="music" :src="musicControl.musicInfo[musicControl.musicCount].musicUrl" />
    </div>
</template>

<script>
    import MusicPlayerTop from "./MusicPlayerTop";
    import MusicPlayerBottom from "./MusicPlayerBottom";
    import MusicShowButton from "./MusicShowButton";
    import { getMusicInfo } from "../../service/api";

    export default {
        name: "MusicPlayer",
        components: {
            MusicPlayerTop,
            MusicPlayerBottom,
            MusicShowButton
        },
        data() {
            return {
                isShow: false,
                isControlShow: false,
                //控制面板
                musicControl: {
                    //控制播放
                    isPlay: false,
                    //是否展开音乐列表
                    isListShow: false,
                    musicInfo: [
                        {
                            musicName: "",
                            musicSinger: "",
                            musicImg: "",
                            musicUrl: ""
                        }
                    ],
                    musicCount: 0
                },
                playerBottom: "playerBottom",
                playerTop: "playerTop"
            };
        },
        created() {
            //请求网络数据
            getMusicInfo()
                .then(response => {
                    this.musicControl.musicInfo = response.data;
                })
                .catch(error => {
                    console.log(error);
                });
        },
        mounted() {
            let musicObj = document.getElementById("music");
            musicObj.addEventListener("ended", () => {
                this.nextSong();
            });
        },
        methods: {
            //音乐面板是否显示
            musicPlanShow() {
                if (this.isShow) {
                    this.isShow = false;
                    setTimeout(() => {
                        this.isControlShow = false;
                    }, 1000);
                } else {
                    this.isShow = true;
                    this.isControlShow = this.isShow;
                }
            },
            //音乐状态
            musicIsPlay() {
                let musicObj = document.getElementById("music");
                if (this.musicControl.isPlay) {
                    this.musicControl.isPlay = false;
                    musicObj.pause();
                } else {
                    this.musicControl.isPlay = true;
                    musicObj.play();
                }
            },
            //控制音乐列表展开
            musicListShow() {
                if (this.musicControl.isListShow) {
                    this.musicControl.isListShow = false;
                } else {
                    this.musicControl.isListShow = true;
                }
            },
            //下一首
            nextSong() {
                if (
                    this.musicControl.musicCount >=
                    this.musicControl.musicInfo.length - 1
                )
                    return;
                this.musicControl.musicCount += 1;
                this.musicControl.isPlay = false;
                let musicObj = document.getElementById("music");
                setTimeout(() => {
                    musicObj.play();
                    this.musicControl.isPlay = true;
                }, 1000);
            },
            //上一首
            previousSong() {
                if (this.musicControl.musicCount <= 0) return;
                let musicObj = document.getElementById("music");
                musicObj.pause();
                this.musicControl.musicCount -= 1;
                musicObj.play();
            },
            //切换音乐
            changeMusic(index) {
                let musicObj = document.getElementById("music");
                if (this.musicControl.musicCount === index) {
                    if (this.musicControl.isPlay) {
                        this.musicControl.isPlay = false;
                        musicObj.pause();
                    } else {
                        this.musicControl.isPlay = true;
                        musicObj.play();
                    }
                } else {
                    this.musicControl.musicCount = index;
                    setTimeout(() => {
                        musicObj.play();
                        this.musicControl.isPlay = true;
                    }, 1000);
                }
            }
        }
    };
</script>

<style scoped>
    .musicPlayer {
        position: fixed;
        top: 400px;
        z-index: 11;
    }
    .musicBody {
        position: absolute;
        left: -335px;
        transition: 1s;
    }
    .playerTop {
        left: 0px;
    }
    .Mbottom {
        height: 0;
        overflow: hidden;
        transition: 1s;
    }
    .playerBottom {
        height: 220px;
    }
    @media screen and (max-width: 768px) {
        .musicPlayer {
            display: none;
        }
    }
</style>