<template>
    <div>
        <el-row>
            <el-scrollbar class="leftPlan" v-if="!isMinScreen">
                <!--背景图-->
                <li class="backgroundImage animated" :class="backIndex===index?'fadeIn':'fadeOut'"
                    v-for="(backImg,index) in backImgs" :key="index"
                    :style="{backgroundImage:'url('+backImg+')'}"></li>
                <div class="mark"></div>
                <about/>
                <!--留言板展示-->
                <Messageboard/>
                <ImageboardPlan :showImage="showImage"
                                :comments="comments"
                                :imageUrl="imageUrl"
                />
                <Footer/>
            </el-scrollbar>

            <div class="rightPlan">
                <Header/>
                <router-view/>
            </div>
        </el-row>
        <MusicPlayer v-if="!isMinScreen"/>
        <!--大图展示区域-->
        <ImageShowWindow
                v-if="!isMinScreen"
                :showImageWindow="showImageWindow"
                :showImage="showImage"
                :imageUrl="imageUrl"
                :nextImage="nextImage"
                :onImage="onImage"
        />
    </div>
</template>

<script>
    import Footer from "./components/Footermassage";
    import Messageboard from "./components/messageboard/Messageboard";
    import about from "./components/about/about";
    import MusicPlayer from "../../components/musicPlayer/MusicPlayer";
    import Article from "./components/article/Article";
    import Header from "../../components/Header";
    import ImageboardPlan from "./components/imageboard/ImageboardPlan";
    import ImageShowWindow from "./components/ImageShowWindow";
    import {getImageInfo} from "../../service/api";
    import isMinScreen from "../../config/isMinScreen";

    export default {
        name: "Home",
        components: {
            Article,
            Footer,
            Messageboard,
            about,
            MusicPlayer,
            Header,
            ImageboardPlan,
            ImageShowWindow
        },
        data() {
            return {
                showImageWindow: false,
                /*照片数据*/
                comments: [],
                backImgs: ["http://pic1.win4000.com/wallpaper/2019-08-05/5d47e530bcf01.jpg",
                    "https://www.qiflyb.cn:3000/public/images/original/5731F4E71E443D2280B605DD4504FBB4.png",
                    "https://www.qiflyb.cn:3000/public/images/original/187A92D3EA417DD788D7764A23A00C4C.png",
                    "https://www.qiflyb.cn:3000/public/images/original/1F89C4976272AEA9ECCC42D02B94DAA0.png"
                ],//背景图
                backIndex: 0,
                imageUrl: "", //点击显示的图片
                isMinScreen: false,
                imageIndex: 0,
                timer: null
            };
        },
        methods: {
            //点击查看大图
            showImage(index) {
                if (this.showImageWindow) {
                    this.showImageWindow = false;
                } else {
                    this.showImageWindow = true;
                    this.imageUrl = this.comments[index].original;
                    this.imageIndex = index;
                }
            },
            //下一张图片
            nextImage(callback) {
                if (this.imageIndex < this.comments.length - 1) {
                    this.imageIndex += 1;
                    this.imageUrl = this.comments[this.imageIndex].original;
                } else {
                    callback();
                }
            },
            //上一张照片
            onImage(callback) {
                if (this.imageIndex > 0) {
                    this.imageIndex -= 1;
                    this.imageUrl = this.comments[this.imageIndex].original;
                } else {
                    callback();
                }
            },
        },
        beforeCreate() {
            isMinScreen((isMinScreen) => {
                this.isMinScreen = isMinScreen;
            });
        },
        created() {
            this.timer = setInterval(() => {
                this.backIndex++;
                if (this.backIndex > this.backImgs.length - 1) {
                    this.backIndex = 0;
                }
            }, 10000);
            //请求网络数据
            getImageInfo()
                .then(response => {
                    this.comments = response.data;
                })
                .catch(error => {
                    console.log(error);
                });
        },
        beforeDestroy() {
            //清除定时器
            clearInterval(this.timer);
        }
    };
</script>

<style lang="scss" scoped>
    @import "../../style/_statement.scss";

    .leftPlan {
        position: fixed;
        left: 0;
        width: 30%;
        height: 100vh;
        overflow-x: hidden;
        color: #fff;
    }

    .backgroundImage {
        position: fixed;
        width: 30%;
        top: 0;
        height: 100vh;
        background: no-repeat center center;
        background-size: cover;
    }

    .mark {
        position: absolute;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.5);
        z-index: 0;
    }

    .rightPlan {
        width: 70%;
        position: absolute;
        right: 0;
    }

    @include styleOtherMPad {
        .leftPlan {
            display: none;
        }

        .rightPlan {
            width: 100%;
        }
    }
</style>
