<template>
    <div class="musicPlayerBottom">
        <div style="width: 100%; height: 20px;"></div>
        <el-scrollbar class="down">
            <ul>
                <li class="music" v-for="(music,index) in musicControl.musicInfo" :key="index">
                    <span>{{music.musicName}} - {{music.musicSinger}}</span>
                    <div class="bottom">
                        <span
                            class="icon-play3"
                            v-show="musicControl.musicCount===index?!musicControl.isPlay:true"
                            @click="changeMusic(index)"
                        ></span>
                        <span
                            class="icon-pause2"
                            v-show="musicControl.musicCount===index?musicControl.isPlay:false"
                            @click="changeMusic(index)"
                        ></span>
                    </div>
                </li>
            </ul>
        </el-scrollbar>
    </div>
</template>

<script>
    export default {
        name: "MusicPlayerBottom",
        props: {
            musicControl: Object,
            changeMusic: Function
        }
    };
</script>

<style scoped>
    .musicPlayerBottom {
        width: 335px;
        height: 220px;
        position: absolute;
        top: 50px;
        background-color: #fff;
        border-radius: 10px;
        box-shadow: 0px 0px 2px 2px rgba(0, 0, 0, 0.2);
    }

    .down {
        width: 100%;
        height: 200px;
        position: absolute;
    }

    .bottom {
        display: none;
    }

    .music {
        width: 100%;
        height: 50px;
        font-size: 14px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0px 30px 0px 10px;
        user-select: none;
        box-sizing: border-box;
    }

    .music:hover .bottom {
        display: block;
    }

    .bottom span {
        font-size: 20px;
        cursor: pointer;
        color: #ff00ad;
    }
</style>