<template>
    <div>
        <div id="mark" v-show="showImageWindow"></div>
        <div class="imageWindow" :class="showImageWindow?'show':''" @click="showImage">
            <div class="image" :style="{backgroundImage:'url('+imageUrl+')'}"></div>
        </div>
        <div v-if="showImageWindow" class="button">
            <button class="leftbutton" @click="onImages"><span class="icon-circle-left"></span></button>
            <button class="rightbutton" @click="nextImages"><span class="icon-circle-right"></span></button>
        </div>
        <div class="point" v-if="fistImage || lastImage">
            <div class="prompt">
                <h3 v-if="lastImage">已经是最后一张了</h3>
                <h3 v-if="fistImage">已经是第一张了</h3>
            </div>

        </div>
    </div>

</template>

<script>
    export default {
        name: "ImageShowWindow",
        data() {
            return {
                fistImage: false,
                lastImage: false
            };
        },
        props: {
            showImageWindow: Boolean,
            showImage: Function,
            imageUrl: String,
            nextImage: Function,
            onImage: Function
        },
        methods: {
            nextImages() {
                this.nextImage(() => {
                    this.lastImage = true;
                    setTimeout(() => {
                        this.lastImage = false;
                    }, 1000);
                });
            },
            onImages(start) {
                this.onImage(() => {
                    this.fistImage = true;
                    setTimeout(() => {
                        this.fistImage = false;
                    }, 1000);
                });
            }
        }
    };
</script>

<style lang="scss" scoped>
    @import "../../../style/_statement";

    #mark {
        position: fixed;
        background-color: rgba(0, 0, 0, 0.5);
        width: 100vw;
        height: 100vh;
    }

    .imageWindow {
        position: fixed;
        left: 50%;
        bottom: 50%;
        width: 0;
        height: 0;
        z-index: 20;
        @include allCenter;
        transition: all 1s;
    }

    .show {
        left: 0;
        bottom: 0;
        width: 100vw;
        height: 100vh;
    }

    .image {
        width: 90%;
        height: 95%;
        @include allCenter;
        background: center center no-repeat;
        background-size: contain;

        img {
            width: 100%;
        }
    }

    .button {
        button {
            outline: none;
            background-color: transparent;
            border: 0;
        }

        span {
            font-size: 60px;
            color: #8c939d;

            &:hover {
                color: #1ecfa4;
            }
        }

        .leftbutton {
            position: fixed;
            top: 50vh;
            left: 10vw;
            z-index: 20;
            margin-top: -30px;
        }

        .rightbutton {
            position: fixed;
            top: 50vh;
            right: 10vw;
            z-index: 20;
            margin-top: -30px;

        }
    }

    .point {
        width: 100%;
        height: 100%;
        font-size: 16px;
        position: fixed;
        z-index: 20;
        @include allCenter.prompt {
            background-color: white;
        }
    }
</style>